.task-node body {
  background-color: transparent;
  overflow: visible;
}
.task-node foreignObject {
  background-color: transparent;
  overflow: visible;
}
.task-node:hover {
  cursor: -webkit-grab;
  cursor: grab;
  background-color: rgba(227, 244, 255, 0.9);
}
.task-node-selected {
  background-color: rgba(227, 244, 255, 0.9);
}
.task-common-node {
  width: 180px;
  height: 30px;
  background-color: hsla(0, 0%, 100%, 0.9);
  border: 1px solid #289de9;
  border-radius: 15px;
  font-size: 12px;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
}
.task-common-node-icon {
  float: left;
  color: #fff;
  font-size: 16px;
  background-color: #289de9;
  width: 26px;
  height: 26px;
  margin: 1px;
  border-radius: 100%;
}
.task-common-node-icon:before {
  float: left;
  margin: 4px;
}
.task-common-node-name {
  float: left;
  margin-left: 2px;
  width: 120px;
  height: 28px;
  line-height: 28px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.task-common-node-status {
  width: 26px;
  height: 26px;
  margin: 1px;
  border-radius: 100%;
  float: right;
  font-size: 18px;
}
.task-common-node-status:before {
  float: left;
  margin: 4px;
}
.task-common-node:hover {
  background-color: rgba(227, 244, 255, 0.9);
}
.task-in-common-ls {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  clear: both;
  margin-top: -34px;
  float: left;
  width: 180px;
}
.task-in-common-ls-wrap {
  height: 1px;
  float: left;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.task-out-common-ls {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: -5px;
  clear: both;
  float: left;
  width: 180px;
}
.task-out-common-ls-wrap {
  height: 1px;
  float: left;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.task-initial-node {
  width: 30px;
  height: 30px;
  border: 1px solid #ccc;
  border-radius: 50%;
}
.task-initial-node-in-wrap {
  clear: both;
  margin-top: -34px;
  float: left;
  width: 30px;
}
.task-initial-node-out-wrap {
  clear: both;
  margin-top: -5px;
  float: left;
  width: 30px;
}
.task-initial-node-name {
  float: left;
  width: 30px;
  height: 28px;
  line-height: 28px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.task-initial-node-icon {
  float: left;
  color: #000;
  font-size: 16px;
  width: 26px;
  height: 26px;
  margin: 1px;
  border-radius: 100%;
}
.task-initial-node-icon:before {
  float: left;
  margin: 4px;
}
.task-initial-node:hover {
  background-color: rgba(227, 244, 255, 0.9);
}
.task-work-area {
  background-color: #fff;
  position: relative;
  overflow: auto;
  cursor: -webkit-grab;
}
.task-work-area::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
.task-work-area::-webkit-scrollbar-thumb {
  background-color: #aeaeae;
  min-height: 50px;
  min-width: 50px;
  border-radius: 10px;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
}
.task-work-area::-webkit-scrollbar-track-piece {
  background-color: #eee;
}
.task-port {
  width: 10px;
  height: 10px;
  float: right;
  margin-right: -5px;
  border: 1px solid gray;
  border-radius: 50%;
  background-color: #fff;
}
.task-port-magnet {
  float: left;
  width: 20px;
  height: 20px;
  margin-top: -6px;
  margin-left: -6px;
  background-color: transparent;
  border-radius: 50%;
}
.task-port-in {
  cursor: default;
}
.task-port-in:hover {
  border: 1px solid #2d8cf0;
  width: 10px;
  height: 10px;
  -webkit-box-shadow: 0px 0px 1px 3px #57a3f3;
          box-shadow: 0px 0px 1px 3px #57a3f3;
  border-radius: 50%;
  margin-top: -1px;
}
.task-in-out {
  border: 1px solid #2d8cf0;
  width: 10px;
  height: 10px;
  -webkit-box-shadow: 0px 0px 1px 3px #57a3f3;
          box-shadow: 0px 0px 1px 3px #57a3f3;
  border-radius: 50%;
  margin-top: -1px;
}
.is-connected {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 4px 0;
  border-color: gray transparent transparent;
  background-color: transparent;
  border-radius: 0;
  margin-top: 6px;
}
.task-port-out {
  cursor: crosshair;
}
.task-tline {
  cursor: default;
}
.task-tline-con {
  fill: none;
  stroke: hsla(0, 0%, 100%, 0);
  stroke-width: 15px;
}
.task-tline-con-wrap {
  fill: none;
  stroke: gray;
  stroke-width: 1px;
}
.task-tline-dotted {
  stroke: rgba(57, 202, 116, 0.8);
  stroke-width: 2px;
  stroke-dasharray: 5;
  -webkit-animation: ant-line 30s infinite linear;
  animation: ant-line 30s infinite linear;
}
.task-tline-hover {
  fill: none;
  stroke: #aeaeae;
  stroke-width: 3px;
}
.task-node-model {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: block;
  border: 1px solid transparent;
  cursor: -webkit-grab;
}
.task-node-model:hover {
  cursor: -webkit-grab;
  cursor: grab;
  background-color: #fff;
  border-color: #289de9;
}
.task-node-model-label {
  height: 26px;
  line-height: 26px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.model-tree {
  padding-left: 15px;
}
.model-tree-node {
  cursor: -webkit-grab;
  cursor: grab;
  padding: 3px 10px;
}
.model-tree li {
  list-style: none;
}
.model-tree-dir {
  cursor: pointer;
  padding: 5px 0;
}
.model-tree-dir:hover {
  background-color: #f5f7fa;
}
